import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import SelectCode from './panels/SelectCode'
import {checkUser, addPhone} from "./api";


class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			user: null,
			home: {
				country: {
					name: "–†–æ—Å—Å–∏—è",
					code: 7
				}
			},
			alert: null
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ user: e.detail.data });

					checkUser(e.detail.data.id).then((res)=>{
						if (res.data.status === "NOT_EXIST"){
							console.log("NEW USER");
							connect.send('VKWebAppGetPhoneNumber');
							connect.send("VKWebAppShowWallPostBox", {
								"message": "–ü–æ—è–≤–∏–ª—Å—è –Ω–æ–≤—ã–π —Å–µ—Ä–≤–∏—Å –≤ –í–ö!üòé\n –ó–¥–µ—Å—å —Ç—ã –º–æ–∂–µ—à—å —É–∑–Ω–∞—Ç—å –∏–º—è —Ç–æ–≥–æ —á–µ–ª–æ–≤–µ–∫–∞," +
									"–∫–æ—Ç–æ—Ä—ã–π –Ω–∞—Å—Ç–æ–π—á–∏–≤–æ –∑–≤–æ–Ω–∏—Ç —Ç–µ–±–µ –ø–æ –Ω–æ—á–∞–ºüò°,\n –∏–ª–∏ –ø—Ä–æ–≤–µ—Ä–∏—Ç—å –Ω–æ–º–µ—Ä, —Å –∫–æ—Ç–æ—Ä–æ–≥–æ " +
									"—Ç–µ–±–µ –∑–≤–æ–Ω–∏–ª/–∞ —Ç–≤–æ–π –ø–∞—Ä–µ–Ω—å/–¥–µ–≤—É—à–∫–∞ü§´,\n –∏–ª–∏ –ø—Ä–æ—Å—Ç–æ —É–∑–Ω–∞—Ç—å –∏–º—è —Ç–æ–≥–æ, –∫—Ç–æ —Ç–µ–±–µ –∑–≤–æ–Ω–∏–ª," +
									"–∞ —Ç—ã –∫–∞–∫ –≤—Å–µ–≥–¥–∞ –Ω–µ —É—Å–ø–µ–ª –ø–æ–¥–Ω—è—Ç—å —Ç—Ä—É–±–∫—Éüò¢\n",
								'attachments': 'https://vk.com/app6888410'
							});
						} else {
							console.log("OLD USER");
						}
					});
					break;
				case 'VKWebAppGetPhoneNumberResult':
					addPhone(
						e.detail.data.phone_number.slice(0,1),
						e.detail.data.phone_number.slice(1),
						`${this.state.user.first_name} ${this.state.user.last_name}`,
						this.state.user.id
					).then((res)=>{
						console.log(`NEW USER >> `);
						console.log(res.data);
					});
					break;
				default:
					console.log(e.detail);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}


	setActivePanel = (id) => {
		this.setState({
			activePanel: id
		})
	};

	setCountry = (code, name) => {
		this.setState({
			home: {
				country: {
					name: name,
					code: code
				}
			}
		})
	};

	setAlert = (alert) => {
		this.setState({alert: alert})
	};

	closeAlert = () => {
		this.setState({alert: null})
	};

	render() {
		return (
			<View activePanel={this.state.activePanel} popout={this.state.alert}>
				<Home
					id="home"
					data={this.state.home}
					setActivePanel={this.setActivePanel}
					setAlert={this.setAlert}
					closeAlert={this.closeAlert}
				/>
				<SelectCode
					id="select_code"
					setCountry={this.setCountry}
					setActivePanel={this.setActivePanel}
				/>
			</View>
		);
	}
}

export default App;
