import React from 'react';
import {
	Panel,
	Button,
	Group,
	PanelHeader,
	SelectMimicry,
	FormLayout,
	Input,
	Footer,
	List,
	Cell,
	Spinner,
	Div,
	Alert

} from '@vkontakte/vkui';
import { getPhone, addPhone } from '../api'


class Home extends React.Component{

	state = {
		data: null,
		inputValue: null,
		isLoading: false,
		inputValueName: null
	};


	selectCode = () => {
		this.props.setActivePanel('select_code')
	};

	addPhoneNumber = () =>{
		this.props.setAlert(
			<Alert
				actionsLayout="horizontal"
				actions={[{
					title: '–û–∫',
					autoclose: true,
					action: () =>{
						if (this.state.inputValueName) {
							addPhone(this.props.data.country.code,this.state.inputValue,this.state.inputValueName).then((res)=>{
								console.log(res.data)
							})
						} else {
							console.log("Number not added!")
						}
						this.setState({
							inputValueName: null
						});
						this.getPhoneData();
					},
					style: 'default'
				}]}
				onClose={this.props.closeAlert}
			>
				<h2>–ù–æ–º–µ—Ä.</h2>
				<p>–í–≤–µ–¥–∏—Ç–µ –≤–ª–∞–¥–µ–ª—å—Ü–∞:</p>
				<Div>
					<Input placeholder="–ò–º—è" value={this.state.inputValueName} onChange={this.updateValueName}/>
				</Div>
			</Alert>
		)
	};
	updateValueName = (evt) => {
		this.setState({inputValueName: evt.target.value})
	};

	getPhoneData = () => {
		this.setState({isLoading:true, data: null});
		if (this.state.inputValue) {
			switch (this.props.data.country.code) {
				case 7:
					if (this.state.inputValue.length === 10) {
						getPhone(this.props.data.country.code,this.state.inputValue).then((res)=>{
							if (res.data.status === "SUCCESS"){
								this.setState({data: res.data, isLoading:false})
							} else {
								this.setState({isLoading:false})
								this.props.setAlert(
									<Alert
										actionsLayout="horizontal"
										actions={[{
											title: '–î–∞',
											autoclose: true,
											action: this.addPhoneNumber,
											style: 'default'
										},{
											title: '–ù–µ—Ç',
											autoclose: true,
											style: 'destructive'
										}]}
										onClose={this.props.closeAlert}
									>
										<h2>–û—à–∏–±–∫–∞.</h2>
										<p>–í–≤–µ–¥–µ–Ω–Ω—ã–π –Ω–æ–º–µ—Ä –Ω–µ –Ω–∞–π–¥–µ–Ω!<br/>–í—ã –∑–Ω–∞–µ—Ç–µ —ç—Ç–æ—Ç –Ω–æ–º–µ—Ä?</p>
									</Alert>
								)
							}
						}).catch((err)=>{
							this.setState({isLoading:false});
							this.props.setAlert(
								<Alert
									actionsLayout="horizontal"
									actions={[{
										title: '–î–∞',
										autoclose: true,
										style: 'default'
									},{
										title: '–ù–µ—Ç',
										autoclose: true,
										style: 'destructive'
									}]}
									onClose={this.props.closeAlert}
								>
									<h2>–û—à–∏–±–∫–∞.</h2>
									<p>–ü—Ä–æ–∏–∑–æ—à–ª–∞ –Ω–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞!</p>
								</Alert>
							)
						})
					} else{
						this.setState({isLoading:false});
						this.props.setAlert(
							<Alert
								actionsLayout="horizontal"
								actions={[{
									title: '–û–∫',
									autoclose: true,
									style: 'default'
								}]}
								onClose={this.props.closeAlert}
							>
								<h2>–û—à–∏–±–∫–∞.</h2>
								<p>–ù–æ–º–µ—Ä –¥–ª—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Ä–µ–≥–∏–æ–Ω–∞ –≤–≤–µ–¥–µ–Ω –Ω–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ!<br/>–ü—Ä–∏–º–µ—Ä:<br/>9150000000<br/>4950000000</p>
							</Alert>
						)
					}
					break;
				default:
					getPhone(this.props.data.country.code,this.state.inputValue).then((res)=>{
						if (res.data.status === "SUCCESS"){
							this.setState({data: res.data, isLoading: false})
						} else {
							this.setState({isLoading:false});
							this.props.setAlert(
								<Alert
									actionsLayout="horizontal"
									actions={[{
										title: '–î–∞',
										autoclose: true,
										action: this.addPhoneNumber,
										style: 'default'
									},{
										title: '–ù–µ—Ç',
										autoclose: true,
										style: 'destructive'
									}]}
									onClose={this.props.closeAlert}
								>
									<h2>–û—à–∏–±–∫–∞.</h2>
									<p>–í–≤–µ–¥–µ–Ω–Ω—ã–π –Ω–æ–º–µ—Ä –Ω–µ –Ω–∞–π–¥–µ–Ω!<br/>–í—ã –∑–Ω–∞–µ—Ç–µ —ç—Ç–æ—Ç –Ω–æ–º–µ—Ä?</p>
								</Alert>
							)
						}
					}).catch((err)=>{
						this.setState({isLoading:false})
						this.props.setAlert(
							<Alert
								actionsLayout="horizontal"
								actions={[{
									title: '–î–∞',
									autoclose: true,
									style: 'default'
								},{
									title: '–ù–µ—Ç',
									autoclose: true,
									style: 'destructive'
								}]}
								onClose={this.props.closeAlert}
							>
								<h2>–û—à–∏–±–∫–∞.</h2>
								<p>–ü—Ä–æ–∏–∑–æ—à–ª–∞ –Ω–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞!</p>
							</Alert>
						)
					})

			}
		} else {
			this.setState({isLoading:false});
			this.props.setAlert(
				<Alert
					actionsLayout="horizontal"
					actions={[{
						title: '–û–∫',
						autoclose: true,
						style: 'default'
					}]}
					onClose={this.props.closeAlert}
				>
					<h2>–û—à–∏–±–∫–∞.</h2>
					<p>–ü–æ–ª–µ –≤–≤–æ–¥–∞ –Ω–æ–º–µ—Ä–∞ - –ø—É—Å—Ç–æ.</p>
				</Alert>
			)
		}
	};

	updateValue = (evt) => {
		this.setState({inputValue: evt.target.value})
	};

	render() {
		return <Panel id={this.props.id}>
			<PanelHeader>–¢–µ–ª–µ—Ñ–æ–Ω–Ω—ã–π —Å–ø—Ä–∞–≤–æ—á–Ω–∏–∫</PanelHeader>

			<Group title="–ü–æ–∏—Å–∫">
				<FormLayout>
					<SelectMimicry
						top="–í—ã–±–µ—Ä–∏—Ç–µ –∫–æ–¥ —Å—Ç—Ä–∞–Ω—ã"
						placeholder="–ù–µ –≤—ã–±—Ä–∞–Ω–∞"
						onClick={this.selectCode}
					>
						{
							this.props.data.country.name ?
								<span> +{this.props.data.country.code} {this.props.data.country.name} </span>
							: null
						}
					</SelectMimicry>
					<Input placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä" type="tel" value={this.state.inputValue ? this.state.inputValue : null} onChange={this.updateValue}/>
					<Button size="xl" level="1" onClick={this.getPhoneData}>–ü–æ–∏—Å–∫</Button>
				</FormLayout>
			</Group>
			{this.state.isLoading ? <Div><Spinner size="medium" style={{ marginTop: 20 }} /></Div>:
				this.state.data ?
					<Group title="–†–µ–∑—É–ª—å—Ç–∞—Ç">
						<List>
							{this.state.data.name.map((item,id)=>{
								return <Cell key={id}>{item}</Cell>
							})}
						</List>
					</Group> : null
			}
			<Footer>–í–≤–µ–¥–∏ –Ω–æ–º–µ—Ä –∏ –Ω–∞–∂–º–∏ –ø–æ–∏—Å–∫, —á—Ç–æ–±—ã —É–∑–Ω–∞—Ç—å –∏–º—è –∞–±–æ–Ω–µ–Ω—Ç–∞.</Footer>
		</Panel>
	}

}
export default Home;