import React from 'react';
import {
    Panel,
    Group,
    PanelHeader,
    List,
    Cell,
    HeaderButton,
    IOS,
    platform,
    Spinner,
    Div
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import { getCode } from '../api';

const osname = platform();

class SelectCode extends React.Component {
    state = {
        list: null,
        from: 0,
        to: 20
    };

    componentDidMount() {
       getCode(this.state.from, this.state.to).then((res) => {
            this.setState({
                list: res.data
            });
            console.log(res.data)
        });
        window.addEventListener('scroll', this.onScroll, false);
    }

    updateList = () => {
        this.setState({from: this.state.to, to: this.state.to + 20});
        getCode(this.state.from, this.state.to).then((res)=>{
            this.setState({list: this.state.list.concat(res.data)});
            if (this.state.to < 200){
                window.addEventListener('scroll', this.onScroll, false);
            }
        });
        window.removeEventListener('scroll', this.onScroll, false);
    };

    onScroll = () => {
        if (
            this.state.list.length && window.scrollY + window.innerHeight >= this.state.list.length*44 - 132
        ) {
            this.updateList();
        }
    };

    render() {
        return <Panel id={this.props.id}>
            <PanelHeader left={
                <HeaderButton onClick={() => this.props.setActivePanel('home')}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>
            }>
                –í—ã–±–æ—Ä —Å—Ç—Ä–∞–Ω—ã
            </PanelHeader>
            <Group>
                    {this.state.list ? <List>
                        {this.state.list.map((item,id)=>{
                            return <Cell key={id}
                                         onClick={() => {
                                             this.props.setCountry( item[0],item[1]);
                                             this.props.setActivePanel('home');
                                         }}
                            >
                                +{item[0]} {item[1]}
                                </Cell>
                        })}
                    </List>:<Div><Spinner size="medium" style={{ marginTop: 20 }} /></Div>}
            </Group>
        </Panel>
    }
}

export default SelectCode