import axios from 'axios';

const main_url = 'https://phonebook.pythonanywhere.com/';

export const getCode = (from, to) => {
    const url = main_url + `gc`;
    return axios.get(
        url,
        {
            params: {
                from: from,
                to: to
            }
        }
    )
};

export const getPhone = (code, phone) => {
    const url = main_url + `gp/${code}/${phone}`;
    return axios.get(
        url
    )
};

export const addPhone = (code, phone, user, vk_id) => {
    const url = main_url + `ap/${code}/${phone}/${user}`;
    return axios.get(
        url,
        {
            params: {
                vk_id: vk_id
            }
        }
    )
};

export const checkUser = (vk_id) => {
    const url = main_url + `cu/${vk_id}`;
    return axios.get(
        url
    )
};